.allView {
    transition:0.2s width;
}
/* Section css */
.section .sectionWrap {
    margin:0 auto;
    margin-block:50px;
    width:95%;
}

.section .caution {
    color:red;
    font-weight:bold;
    text-align:center;
    margin-bottom:10px;
}

.section .sectionSub {
    display:flex;
    flex-direction:row;
    width:90%;
    box-shadow:5px 3px 7px 3px gray;
}
    /* Aside css */
.section .aside {
    padding:10px;
    width:450px;
    border-top-right-radius:50px;
    background:#6E3482;
    transition:0.2s width;
}

.section .asideWrap {
    display:flex;
    flex-direction:column;
}

.section .asideWrap a {
    display:flex;
    align-items:center;
    width:max-content;
    font-size:18px;
    margin-bottom:50px;
    color:black;
}

.section .asideWrap a:hover {
    font-weight:bold;
}

.section .asideWrap a span:last-child {
    margin-left:5px;
}

.section .search {
    margin-bottom:50px;
    display:flex;
    flex-direction:column;
    align-items:center;
}

.section .search p {
    text-align:center;
    font-size:20px;
    font-weight:bold;
    margin-bottom:5px;
    transition:0.2s font-size;
}

.section .searchWrap {
    position:relative;
    display:flex;
    align-items:center;
    width:440px;
    height:45px;
    border-top-right-radius:10px;
    border-bottom-right-radius:10px;
    background:#e7e1da;
    transition:0.2s width;
}

.section .searchWrap input {
    padding-left:20px;
    width:370px;
    height:inherit;
    outline:none;
    border:none;
    background:none;
    font-size:18px;
    font-weight:bold;
    transition:0.2s width;
}

.section .searchWrap div {
    flex:1;
    display:flex;
    justify-content:center;
    align-items:center;
    height:100%;
}

.section .searchWrap .searchIcon {
    font-size:25px;
    color:black !important;
}

.section .searchWrap div:hover {
    cursor:pointer;
}

.section .genresType {
    margin-bottom:10px;
    text-align:center;
    font-size:20px;
    font-weight:bold;
}

.section .genres {
    display:grid;
    grid-template-columns:repeat(2, 1fr);
    grid-row-gap:15px;
}

.section .genres div {
    margin-left:30px;
}

.section .genres input {
    display:none;
}

.section .genres label {
    display:flex;
    align-items:center;
    width:max-content;
    font-size:18px;
}

.section .genres label::before {
    margin-right:10px;
    content:"✔";
    display:flex;
    justify-content:center;
    align-items:end;
    width:30px;
    height:30px;
    font-size:25px;
    font-weight:bold;
    border-radius:5px;
    color:#a56abd46;
    background:rgba(0,0,0,0.3);
    transition:0.2s background-color;
}

.section .genres input:checked + label::before {
    content:"✔";
    color:#A56ABD;
    background:#E7DBEF;
}

.section .genres label:hover {
    cursor:pointer;
}

.section .select {
    margin-top:50px;
    display:flex;
    flex-direction:column;
}

.section .select select {
    margin-bottom:20px;
    height:50px;
}
    /* //Aside css */
    
    /* Content css */
.content {
    width:100%;
}

.content .rating {
    display:flex;
    justify-content:space-around;
}

.content .rating li {
    margin-block:30px;
    border:1px solid #6E3482;
    border-radius:50px;
    box-sizing:border-box;
    background:#f5ebfa;
}

.content .rating input {
    display:none;
}

.content .rating label {
    position:relative;
    padding-inline:15px;
    padding-block:5px;
    display:flex;
    justify-content:center;
    align-items:center;
    color:black !important;
}

.content .rating label::before {
    margin-right:10px;
    content:"";
    display:flex;
    justify-content:center;
    align-items:center;
    width:20px;
    height:20px;
    border:1px solid #6f34828f;
    box-sizing:border-box;
    border-radius:50%;
}

.content .rating label::after {
    position:absolute;
    left:19px;
    content:"";
    display:block;
    width:12px;
    height:12px;
    border-radius:50%;
    background:rgba(0,0,0,0.3);
}


.content .rating input:checked + label::before {
    border-color:#6E3482;
}

.content .rating input:checked + label::after {
    background:#A56ABD;
}

.content .rating label:hover {
    cursor:pointer;
}

.content .contentWrap {
    display:grid;
    grid-template-columns:repeat(4, 1fr);
    grid-row-gap:50px;
    justify-items:center;
    align-items:start;
}

.content .contentWrap li {
    position:relative;
    display:flex;
    flex-direction:column;
    align-items:center;
    margin-top:20px;
    border-radius:10px;
    box-shadow:0px 0px 5px 2px gray;
    transition:0.2s transform;
}

.content .contentWrap li:hover {
    cursor:pointer;
    transform:scale(1.1);
}

.content .contentWrap img {
    width:200px;
    height:300px;
    border-radius:10px;
}

.content .posterBtm {
    padding:5px;
    display:flex;
    justify-content:space-between;
    align-items:center;
    margin-right:5px;
}

.content .title {
    width:150px;
    font-weight:bold;
    text-overflow:ellipsis;
    white-space:nowrap;
    overflow:hidden;
}

.content .date .month {
    margin-right:5px;
    color:rgb(97, 97, 97) !important;
}

.content .date .day {
    font-size:14px;
    color:rgb(97, 97, 97) !important
}

.content .date .dDay {
    margin-left:10px;
    color:red !important;
    font-weight:bold;
    text-shadow:1px 1px 1px gray;
}

.content .grade {
    display:flex;
    align-items:center;
}

.content .grade span {
    margin-left:3px;
    line-height:20px;
    font-weight:bold;
}

.content .page {
    margin-block:50px;
    display:flex;
    justify-content:center;
}

.content .page ul {
    display:flex;
}

.content .page ul li {
    font-size:20px;
    padding-inline:5px;
}

.content .page ul li span {
    display:flex;
    align-items:center;
    height:100%;
}

.content .page ul li span:hover {
    cursor:pointer;
    transform:scale(1.2);
}
    /* //Content css */
/* //Section css */

@media (max-width:1600px) {
    .section .sectionSub {
        display:flex;
        flex-direction:row;
        box-shadow:5px 3px 7px 3px gray;
    }

    .content .contentWrap {
        grid-template-columns:repeat(3, 1fr);
    }
}

@media (max-width:1500px) {
    .allView {
        width:1280px;
    }

    .section .aside {
        width:250px;
    }

    .section .search p {
        font-size:17px;
    }

    .section .searchWrap {
        width:200px;
    }

    .section .searchWrap input {
        width:140px;
    }

    .section .genres {
        grid-template-columns:repeat(1, 1fr);
    }

    .content .rating li {
        width:max-content;
    }
}