/* Header css */
.headerWrap {
    position:relative;
    margin:0 auto;
    width:95%;
}

.headerWrap h1 {
    text-align:center;
    padding-block:20px;
    font-size:50px;
    color:#49225B;
}

.headerWrap button {
    position:absolute;
    right:100px;
    top:40px;
    display:flex;
    justify-content:center;
    align-items:center;
    width:100px;
    height:40px;
    border:1px solid #111;
    background:black;
    color:white;
    font-weight:bold;
    border-radius:50px;
    box-sizing:border-box;
}

.headerWrap button:hover {
    cursor:pointer;
}

.header a {
    position:fixed;
    bottom:50px;
    right:145px;
    display:flex;
    justify-content:center;
    align-items:center;
    width:100px;
    height:40px;
    border-radius:10px;
    font-size:15px;
    color:black;
    border:1px solid #111;
    box-sizing:border-box;
    background:white;
    z-index:10;
}

.header a:hover {
    cursor:pointer;
    background:rgb(250, 250, 250);
}
/* //Header css */

/* Section css */
.section .sectionWrap {
    margin:0 auto;
    display:flex;
    flex-direction:column;
    align-items:center;
    width:95%;
}

.section .poster {
    margin-block-start:100px;
    margin-block-end:30px;
}

.section .posterTop {
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    align-items:center;
    width:970px;
    margin-bottom:30px;
}

.section .posterTop h4 {
    display:flex;
    justify-content:space-between;
    align-items:center;
    width:250px;
    font-size:30px;
}

.section .posterTop span {
    font-size:25px;
}

.section .posterTop .link {
    display:flex;
    justify-content:center;
    align-items:center;
    width:100px;
    height:30px;
    border:1px solid rgba(0,0,0,0.3);
    border-radius:5px;
    box-shadow:0px 1px 3px 0.1px gray;
    box-sizing:border-box;
    color:black;
    background:white;
}

.section .posterTop .link:hover {
    box-shadow:0px 1px 3px 0.3px gray;
}

.section .movie {
    display:flex;
    flex-direction:row;
    align-items:center;
    width:980px;
    overflow:hidden;
    overflow-x:scroll;
}

.section .movie::-webkit-scrollbar {
    border-radius:20px;
    background-color:rgba(224, 224, 224, 0.1);
}

.section .movie::-webkit-scrollbar-thumb {
    border-radius:20px;
    background-color:rgba(224, 224, 224, 0.5);
}

.section .movie::-webkit-scrollbar-button {
    display:none;
}

.section .movie li {
    position:relative;
    margin-right:50px;
}

.section .movie li:last-child {
    margin-right:0;
}

.section .movie img {
    border-radius:10px;
    height:300px;
}

.section .movie img:hover {
    cursor:pointer;
}

.section .movie .rankNumber {
    position:absolute;
    bottom:33px;
    left:5px;
    font-size:50px;
    color:white !important;
    font-style:italic;
    font-weight:bold;
}

.section .posterBtm {
    display:flex;
    width:100%;
}

.section .movie .title {
    width:130px;
    font-weight:bold;
    white-space:nowrap;
    text-overflow:ellipsis;
    overflow:hidden;
}

.section .grade {
    display:flex;
    align-items:center;
    margin-left:auto;
    align-self:center;
    font-size:20px;
}

.section .posterBtm .grade span {
    font-weight:bold;
    margin:0px 10px 0px 5px;
}

.section .date .month {
    margin-right:5px;
    color:rgb(97, 97, 97) !important;
}

.section .date .day {
    font-size:14px;
    color:rgb(97, 97, 97) !important;
}

.section .date .dDay {
    margin-left:15px;
    color:red !important;
    font-weight:bold;
    text-shadow:1px 1px 1px gray;
}
/* //Section css */