/* Detail css */
.detail {
    position:fixed;
    top:0;
    display:flex;
    justify-content:center;
    align-items:center;
    width:100vw;
    height:100vh;
    background:rgba(0,0,0,0.2);
    z-index:10;
}

.detail .detailWrap {
    display:flex;
    justify-content:center;
    align-items:center;
    width:1000px;
    height:600px;
    border-radius:10px;
    box-sizing:border-box;
    box-shadow:0px 0px 10px 1px gray;
    background:white;
}

.detail .detailSub {
    display:flex;
    flex-direction:row;
    width:900px;
    height:550px;
}

.detail .detailSub img {
    border-radius:10px;
    box-sizing:border-box;
}

.detail .ctn {
    position:relative;
    flex:1;
    display:flex;
    padding-top:30px;
}

.detail .content {
    width:100%;
    margin-left:30px;
    height:400px;
}

.detail .content .top {
    display:flex;
    justify-content:space-between;
    align-items:center;
}

.detail .content .top .right {
    display:flex;
}

.detail .content .popularity,
.detail .content .vote {
    padding:5px;
    display:flex;
    align-items:center;
    font-size:20px;
    line-height:20px;
    margin-right:10px;
    border:1px solid #747171;
    border-radius:5px;
    box-sizing:border-box;
}

.detail .content .voteIcon {
    color:#463e3e;
}

.detail .content .popularity span,
.detail .content .vote span {
    font-weight:bold;
    margin-left:5px;
}

.detail .content .title {
    font-size:25px;
    font-weight:bold;
}

.detail .content ul {
    display:flex;
}

.detail .content ul li {
    margin:10px 10px 30px 0px;
    padding:5px;
    width:max-content;
    border:1px solid #111;
    border-radius:30px;
    box-sizing:border-box;
    background:rgb(255, 221, 227);
}

.detail .content ul li span {
    color:black;
}

.detail .content fieldset {
    border:2px solid #9b9b9b;
    box-sizing:border-box;
}

.detail .content fieldset legend {
    font-weight:bold;
    font-size:18px;
}

.detail .content .overview { 
    line-height:30px;
    font-size:15px;
}
/* //Detail css */