body {user-select:none;}

.dark-mode {
    color:white;
    background:black;
    transition:unset
}

.dark-mode span {
    color:white;
}

.dark-mode button {
    background: white;
    color: black;
}

.dark-mode .brightBtn {
    width:110px;
    color:black;
    background:white;
}

div, ul, li, img, p, h1, h4, input, label, a {margin:0; padding:0}

a {text-decoration:none;}

li {list-style:none;}

.loading {
    position:absolute;
    top:50%;
    left:50%;
    transform:translate(-50%, -50%);
}

.heartIcon {
    color:red;
}

body,
button,
#Link {
    transition:background-color 0.2s, color 0.2s;
}